  background-color: #fff;
  background-image: none;
  margin: 0;
  padding: 0;
  font-family: ui-sans-serif, system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
  font-size: 100%;
}

.btn-52:disabled {
  cursor: default;
}

.btn-52:-moz-focusring {
  outline: auto;
}

.btn-52 svg {
  display: block;
}

.btn-52 [hidden] {
  display: none;
}

.btn-52 {
  box-sizing: border-box;
  text-transform: uppercase;
  border-radius: 999px;
  padding: .7rem 3rem;
  font-weight: 900;
  display: block;
  position: relative;
  overflow: hidden;
}

.btn-52 .original {
  color: #fff;
  display: -ms-grid;
  -o-transition: transform .2s;
  background: #000;
  align-content: center;
  justify-content: center;
  transition: transform .2s, -webkit-transform .2s;
  display: grid;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.btn-52:hover .original {
  transform: translateY(100%);
}

.btn-52 .letters {
  display: inline-flex;
}

.btn-52 span {
  opacity: 0;
  -o-transition: transform .2s, opacity .2s;
  transition: transform .2s, opacity .2s, -webkit-transform .2s;
  transform: translateY(-15px);
}

.btn-52:hover span {
  opacity: 1;
  transform: translateY(0);
}

.btn-52:hover span:nth-child(2) {
  -o-transition-delay: .1s;
  transition-delay: .1s;
}

.btn-52:hover span:nth-child(3) {
  -o-transition-delay: .2s;
  transition-delay: .2s;
